.media {
  height: 250px;
  width: 100%;
  object-fit: cover;
  z-index: 0;
}

.container {
  position: relative;
}

.overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  height: 100%;
  width: 100%;
  transition: 0.5s ease;
  z-index: 1;
  cursor: pointer;
}

.container:hover .overlay {
  opacity: 1;
}

.heading {
  background-image: linear-gradient(
      rgba(255, 255, 255, 0.15),
      rgba(255, 255, 255, 0.15)
    ),
    url(/static/media/gehry.07e0ffe3.jpg);
  background-position: center center;
  background-size: cover;
}

.nav-bar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: black;
  z-index: 2;
}

.divider {
  background-color: black;
}

.slides {
  background-image: linear-gradient(#86b0dd, #eed2c8);
  background-position: center center;
  background-size: cover;
}

html {
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
}

