.heading {
  background-image: linear-gradient(
      rgba(255, 255, 255, 0.15),
      rgba(255, 255, 255, 0.15)
    ),
    url('../../images/gehry.jpg');
  background-position: center center;
  background-size: cover;
}

.nav-bar {
  position: sticky;
  top: 0;
  background-color: black;
  z-index: 2;
}

.divider {
  background-color: black;
}

.slides {
  background-image: linear-gradient(#86b0dd, #eed2c8);
  background-position: center center;
  background-size: cover;
}
